@use 'variables' as *;

$zwssgrslider: ".zwssgr";

#{$zwssgrslider} { 
    &-slider {
        max-width: 100%;
        height: fit-content;
        position: relative;
        padding: 0 30px;
        
        #{$zwssgrslider} { 
            &-slide-item {
                background: #FAFAFA;
                border-radius: var(--border-radius-large);
                height: auto;
        
                &:focus {
                    outline: none;
                }
            }

            &-list-inner {
                background-color: #FAFAFA;
                padding: 20px;
                border-radius: var(--border-radius-large);
                height: 100%;
            }

            &-rating {
                width: 90px;
            }

            &-rating-wrap {
                display: flex;
                flex-wrap: wrap;
                margin-bottom: 20px;
                align-items: center;
            }

            &-slide-wrap4 {
                display: flex;
                justify-content: space-between;
                align-items: center;
                flex-wrap: wrap;
                margin-top: 25px;
                #{$zwssgrslider}{  
                    &-google-icon {
                        position: absolute;
                        width: 20px;
                        bottom: 0;
                        right: 0;

                        img {
                            width: 20px;
                            height: 20px;
                        }
                    }

                    &-review-info {
                        flex: 1 0 0%;
                        padding-left: 20px;
                    }
                }
              
            }

            &-title {
                font-size: var(--font-size-body);
                color: var(--body-color);
                font-family: var(--font-primary);
                font-weight: var(--font-weight-700);
                margin: 0 0 5px 0;
            }

            &-days-ago {
                font-size: var(--font-size-14);
                color: var(--gray-color);
                font-family: var(--font-primary);
                font-weight: var(--font-weight-400);
                margin: 0;
                line-height: normal;
            }
    
            &-star {
                color: #ccc;
                font-size: 20px;
        
                &.filled {
                    color: var(--tertiary-color);
                }
            }
            
            &-content {
                font-size: var(--font-size-14);
                color: var(--body-color);
                font-family: var(--font-primary);
                font-weight: var(--font-weight-400);
                margin: 10px 0;
            }

            &-profile {
                width: 62px;
                position: relative;
    
                img {
                    width: 62px;
                    height: 62px;
                    border-radius: 100%;
                }
            }

            &-slide-wrap {
                display: flex;
                justify-content: space-between;
                align-items: center;
                flex-wrap: wrap;
                margin-bottom: 10px;
    
                #{$zwssgrslider} {
                    &-review-info {
                        width: calc(100% - 120px);
                    }
    
                   &-google-icon {
                        width: 20px;
                        height: 20px;
            
                        img {
                            width: 20px;
                            height: 20px;
                        }
                    }
                }
            }

            &-swiper-button-next,
            &-swiper-button-prev {
                &::after {
                    font-size: 30px;
                    font-weight: 900;
                    color: #3A4554;
                }
            }

            &-swiper-button-next {
                right: 0;
            }

            &-swiper-button-prev {
                left: 0;
            }
        }
    
    }

    &-slider3 {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        align-items: center;

        .zwssgr-slider-badge {
            width: 240px;
            text-align: center;
        }

        .zwssgr-slider-3-wrap {
            width: calc(100% - 280px);
        }
    }

    &-slider4 {
        max-width: 650px;
        margin: 0 auto;
        padding: 0 30px;
    }

    &-slider5 {
        max-width: 750px;
        margin: 0 auto;
        text-align: center;

        #{$zwssgrslider} { 
            &-profile {
                margin: 0 auto;
            }
            &-slide-item {
                background-color: transparent;
                padding: 0;
            }
            &-contnt-wrap {
                background-color: #FAFAFA;
                padding: 20px;
                border-radius: var(--border-radius-large);
            }
            &-rating {
                margin: 0 auto 30px;
            }
            &-title {
                margin-top: 20px;
            }

            &-google-icon {
                position: absolute;
                width: 20px;
                bottom: 0;
                right: 0;

                img {
                    width: 20px;
                    height: 20px;
                }
            }
        }
    }

    &-slider6 {
        #{$zwssgrslider} {
            &-content {
                text-align: center;
            }
    
            &-rating {
                margin: 0 auto 10px;
            }
        }
        
    }
     
}





   
    
  



   


  

    

   


  

    


