:root {
  /* Fonts */
  --font-primary: "Poppins", sans-serif;
  /* Colors */
  --primary-color: #3caab4;
  --secondary-color: #0074A2;
  --tertiary-color: #F08C3C;
  --gray-color: #8E8E8E;
  --body-color: #1B1B1B;
  --white-color: #FFFFFF;
  --black-color: #000000;
  /* Font Sizes */
  --font-size-h1: 20px;
  --font-size-h2: 18px;
  --font-size-body: 16px;
  --font-size-14: 14px;
  /* Font Weights */
  --font-weight-700: 700;
  --font-weight-600: 600;
  --font-weight-500: 500;
  --font-weight-400: 400;
  /* Border Radius */
  --border-radius-large: 8px;
  --border-radius: 4px;
}

.zwssgr-submit-btn {
  padding: 20px 24px;
  border-radius: var(--border-radius);
  border: 1px solid var(--secondary-color);
  background-color: var(--white-color);
  color: var(--secondary-color);
  font-size: var(--font-size-body);
  font-weight: var(--font-weight-500);
  font-family: var(--font-primary);
  line-height: 1;
  cursor: pointer;
}
.zwssgr-submit-btn:hover {
  background-color: var(--secondary-color);
  color: var(--white-color);
}
.zwssgr-btn {
  background-color: var(--tertiary-color);
  padding: 20px 24px;
  border-radius: var(--border-radius);
  color: #102932;
  font-size: var(--font-size-body);
  font-weight: var(--font-weight-500);
  font-family: var(--font-primary);
  border: 1px solid #F5B976;
  cursor: pointer;
}
.zwssgr-btn:hover {
  background-color: var(--secondary-color);
  border-color: var(--secondary-color);
  color: var(--white-color);
}
.zwssgr-select-btn {
  padding: 10px;
  font-size: var(--font-size-14);
}

.zwssgr-toogle-display {
  background-color: #e0e0e0;
  border: 1px solid var(--border-gray);
  padding: 30px;
  margin: 0 auto;
  text-align: center;
}
.zwssgr-option-item {
  display: none;
  margin: 20px 0;
  padding: 20px;
  background-color: var(--white-color);
  box-shadow: 0px 0.5px 2px 0px rgba(37, 42, 59, 0.1098039216);
  border-radius: var(--border-radius-large);
}
.zwssgr-option-item.selected {
  border: 5px solid #285064;
}
.zwssgr-option-item.selected[data-type=badge] {
  background-color: var(--primary-color);
}
.zwssgr-option-item.selected[data-type=badge] .zwssgr-layout-title {
  color: var(--black-color);
}

.zwssgr-radio {
  box-sizing: border-box;
}
.zwssgr-layout-radio {
  display: flex;
  align-items: center;
  padding: 20px;
  background-color: var(--white-color);
  box-shadow: 0px 0.5px 2px 0px rgba(37, 42, 59, 0.1098039216);
  border-radius: 16px;
  flex-wrap: wrap;
}
.zwssgr-layout-radio label {
  font-size: 15px;
  font-family: var(--font-primary);
  font-weight: var(--font-weight-400);
  line-height: 1;
  margin-right: 30px;
}
.zwssgr-nav-tab-wrapper {
  border-bottom: 1px solid rgb(221, 225, 231);
}
.zwssgr-nav-tab-wrapper .nav-tab {
  background: none;
  box-shadow: none;
  border: none;
  color: #285064;
  font-weight: var(--font-weight-500);
  font-size: var(--font-size-body);
  font-family: var(--font-primary);
  padding-bottom: 15px;
}
.zwssgr-nav-tab-wrapper .nav-tab-active {
  border-bottom: 3px solid #11AAB4;
}
.zwssgr-tab-item {
  font-size: var(--font-size-14);
  font-weight: var(--font-weight-400);
  color: #4A494B;
  font-family: var(--font-primary);
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  cursor: pointer;
  pointer-events: none;
  text-transform: capitalize;
  padding-bottom: 10px;
}
.zwssgr-tab-item .zwssgr-step {
  font-weight: var(--font-weight-600);
  color: var(--body-color);
  margin-right: 10px;
  position: relative;
  width: 18px;
  height: 19px;
  background: #f0f0f1;
  border-radius: 200%;
  display: flex;
  justify-content: center;
  align-items: center;
  line-height: 1;
}
.zwssgr-tab-item .zwssgr-step.done {
  font-size: 0;
}
.zwssgr-tab-item .zwssgr-step.done:before {
  content: url("../../images/step-done-icon.png");
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
  height: 18px;
  width: 18px;
  margin: 0;
}
.zwssgr-tab-item.done {
  pointer-events: all;
}
.zwssgr-tab-item.done .zwssgr-step {
  font-size: 0;
}
.zwssgr-tab-item.done .zwssgr-step:before {
  content: url("../../images/step-done-icon.png");
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
  height: 18px;
  width: 18px;
  margin: 0;
}
.zwssgr-tab-item.active {
  color: #285064;
  font-weight: var(--font-weight-600);
  pointer-events: all;
}
.zwssgr-custom-tab {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  margin: 30px 0;
}
.zwssgr-custom-tab .zwssgr-step-arrow {
  position: relative;
  margin: 0px 20px;
}
.zwssgr-custom-tab .zwssgr-step-arrow:before, .zwssgr-custom-tab .zwssgr-step-arrow:after {
  content: "";
  background: #4A494B;
  position: relative;
  width: 10px;
  height: 2px;
  display: block;
  transform: rotate(45deg);
  border-radius: 2px;
  top: 6px;
}
.zwssgr-custom-tab .zwssgr-step-arrow:after {
  transform: rotate(-45deg);
  top: 10px;
}
.zwssgr-tab-content-display {
  display: none;
}
.zwssgr-google-tab-text {
  font-size: var(--font-size-body);
  font-weight: var(--font-weight-500);
  font-family: var(--font-primary);
  line-height: 1.6;
  color: red;
}
.zwssgr-tab-content {
  display: none;
  padding-right: 1rem;
}
.zwssgr-tab-content.active {
  display: block;
}

.zwssgr-layout-title-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
.zwssgr-layout-title {
  font-size: var(--font-size-h1);
  color: var(--primary-color);
  font-weight: var(--font-weight-500);
  font-family: var(--font-primary);
  text-transform: capitalize;
}

.zwssgr-section-wrap {
  border-radius: var(--border-radius-large);
  box-shadow: 0px 0.5px 2px 0px rgba(37, 42, 59, 0.1098039216);
  background: var(--white-color);
  padding: 20px;
  margin-top: 20px;
}
.zwssgr-section-wrap form h2 {
  display: none;
}

.zwssgr-widget-wrap {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
}
.zwssgr-widget-wrap .zwssgr-widget-setting {
  width: 45%;
}
.zwssgr-widget-wrap .zwssgr-widget-eleemt-list {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
}
.zwssgr-widget-wrap .zwssgr-widget-eleemt-list li {
  padding-bottom: 20px;
  width: 33.33%;
}

.zwssgr-slider {
  max-width: 100%;
  height: fit-content;
  position: relative;
  padding: 0 30px;
}
.zwssgr-slider .zwssgr-slide-item {
  background: #FAFAFA;
  border-radius: var(--border-radius-large);
  height: auto;
}
.zwssgr-slider .zwssgr-slide-item:focus {
  outline: none;
}
.zwssgr-slider .zwssgr-list-inner {
  background-color: #FAFAFA;
  padding: 20px;
  border-radius: var(--border-radius-large);
  height: 100%;
}
.zwssgr-slider .zwssgr-rating {
  width: 90px;
}
.zwssgr-slider .zwssgr-rating-wrap {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
  align-items: center;
}
.zwssgr-slider .zwssgr-slide-wrap4 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 25px;
}
.zwssgr-slider .zwssgr-slide-wrap4 .zwssgr-google-icon {
  position: absolute;
  width: 20px;
  bottom: 0;
  right: 0;
}
.zwssgr-slider .zwssgr-slide-wrap4 .zwssgr-google-icon img {
  width: 20px;
  height: 20px;
}
.zwssgr-slider .zwssgr-slide-wrap4 .zwssgr-review-info {
  flex: 1 0 0%;
  padding-left: 20px;
}
.zwssgr-slider .zwssgr-title {
  font-size: var(--font-size-body);
  color: var(--body-color);
  font-family: var(--font-primary);
  font-weight: var(--font-weight-700);
  margin: 0 0 5px 0;
}
.zwssgr-slider .zwssgr-days-ago {
  font-size: var(--font-size-14);
  color: var(--gray-color);
  font-family: var(--font-primary);
  font-weight: var(--font-weight-400);
  margin: 0;
  line-height: normal;
}
.zwssgr-slider .zwssgr-star {
  color: #ccc;
  font-size: 20px;
}
.zwssgr-slider .zwssgr-star.filled {
  color: var(--tertiary-color);
}
.zwssgr-slider .zwssgr-content {
  font-size: var(--font-size-14);
  color: var(--body-color);
  font-family: var(--font-primary);
  font-weight: var(--font-weight-400);
  margin: 10px 0;
}
.zwssgr-slider .zwssgr-profile {
  width: 62px;
  position: relative;
}
.zwssgr-slider .zwssgr-profile img {
  width: 62px;
  height: 62px;
  border-radius: 100%;
}
.zwssgr-slider .zwssgr-slide-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 10px;
}
.zwssgr-slider .zwssgr-slide-wrap .zwssgr-review-info {
  width: calc(100% - 120px);
}
.zwssgr-slider .zwssgr-slide-wrap .zwssgr-google-icon {
  width: 20px;
  height: 20px;
}
.zwssgr-slider .zwssgr-slide-wrap .zwssgr-google-icon img {
  width: 20px;
  height: 20px;
}
.zwssgr-slider .zwssgr-swiper-button-next::after, .zwssgr-slider .zwssgr-swiper-button-prev::after {
  font-size: 30px;
  font-weight: 900;
  color: #3A4554;
}
.zwssgr-slider .zwssgr-swiper-button-next {
  right: 0;
}
.zwssgr-slider .zwssgr-swiper-button-prev {
  left: 0;
}
.zwssgr-slider3 {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
}
.zwssgr-slider3 .zwssgr-slider-badge {
  width: 240px;
  text-align: center;
}
.zwssgr-slider3 .zwssgr-slider-3-wrap {
  width: calc(100% - 280px);
}
.zwssgr-slider4 {
  max-width: 650px;
  margin: 0 auto;
  padding: 0 30px;
}
.zwssgr-slider5 {
  max-width: 750px;
  margin: 0 auto;
  text-align: center;
}
.zwssgr-slider5 .zwssgr-profile {
  margin: 0 auto;
}
.zwssgr-slider5 .zwssgr-slide-item {
  background-color: transparent;
  padding: 0;
}
.zwssgr-slider5 .zwssgr-contnt-wrap {
  background-color: #FAFAFA;
  padding: 20px;
  border-radius: var(--border-radius-large);
}
.zwssgr-slider5 .zwssgr-rating {
  margin: 0 auto 30px;
}
.zwssgr-slider5 .zwssgr-title {
  margin-top: 20px;
}
.zwssgr-slider5 .zwssgr-google-icon {
  position: absolute;
  width: 20px;
  bottom: 0;
  right: 0;
}
.zwssgr-slider5 .zwssgr-google-icon img {
  width: 20px;
  height: 20px;
}
.zwssgr-slider6 .zwssgr-content {
  text-align: center;
}
.zwssgr-slider6 .zwssgr-rating {
  margin: 0 auto 10px;
}